<template>
	<view>
		<cu-custom bgColor="bg-gradual-blue" :isBack="true">
			<block slot="content">座位选择列表</block>
		</cu-custom>
		<!-- <homecom class="homclass"></homecom> -->
		<view class="mainsub">
			<scroll-view scroll-x class="bg-white nav text-center">
				<view class="cu-item" :class="index==TabCur?'text-blue cur':''" v-for="(item,index) in tabArr"
					:key="index" @tap="tabSelect" :data-id="index">
					{{item}}
				</view>
			</scroll-view>
			<view class="text-xsl padding" v-if="listdata.length == 0">
				<text class=" cuIcon-info text-blue"></text>
				<view class="textclass">没有可查看的数据</view>
			</view>
			<view class="card-main" v-for="(item,i) in listdata" :key="i" style="margin-bottom: 20px;">
				<view class="card-top">
					<view class="card-toptitle01">
						<view class="card-content">
							<view class="card-maintitle01">
								座位名称：
							</view>
							<view class="card-maintitle02">
								{{item.seatdesc}}
							</view>
						</view>
					</view>
					<view class="card-toptitle02" v-if="TabCur == 0">
						<text style="color: cornflowerblue;font-weight: bold;" @click="updata(item.id)">预约</text>
					</view>
				</view>
				<view class="card-top">
					<view class="card-toptitle01">
						<view class="card-content">
							<view class="card-maintitle01">
								座位地址：
							</view>
							<view class="card-maintitle02">
								{{item.address}}
							</view>
						</view>
					</view>
				</view>
				<view class="card-top">
					<view class="card-toptitle01">
						<view class="card-content">
							<view class="card-maintitle01">
								预约信息：
							</view>
							<view class="card-maintitle02">
								{{item.starttime}}至{{item.endtime}}
							</view>
						</view>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import homecom from "@/components/homecom/homecom.vue"
	export default {
		onLoad(options) {
			this.form.floortext = options.floortext
			this.form.starttime = options.starttime
			this.form.endtime = options.endtime
			this.form.floor = options.floor
		},
		mounted() {
			this.init()
		},
		components: {
			homecom
		},
		data() {
			return {
				listdata: [],
				tabArr: ["可选择", "不可选择", "已选择"],
				TabCur: 0,
				form: {
					floortext: '',
					starttime: '',
					endtime: '',
					floor: ''
				}
			}
		},
		methods: {
			async init() {
				this.queryseat(0)
			},
			async queryseat(index) {
				let queryobj = {
					floor: this.form.floor,
					starttime: this.form.starttime,
					endtime: this.form.endtime,
				}
				if (index == 1) {
					let resdata = await new this.Request(this.Urls.m().bystatusnull, queryobj).modepost()
					this.listdata = resdata
				}
				if (index == 0) {
					let resdata = await new this.Request(this.Urls.m().seatbystatuszc, queryobj).modepost()
					this.listdata = resdata.filter((val, i) => {
						return val.starttime == null && val.endtime == null
					})
				}
				if (index == 2) {
					let resdata = await new this.Request(this.Urls.m().seatbystatuszc, queryobj).modepost()
					this.listdata = resdata.filter((val, i) => {
						return val.starttime != null || val.endtime != null
					})
				}
			},
			gosearch(val) {
				uni.navigateTo({
					url: `../search/search?type=${val}`
				});
			},
			async updata(id) {
				let obj = {
					id,
					starttime: this.form.starttime,
					endtime: this.form.endtime,
				}
				let resdata = await new this.Request(this.Urls.m().edittime, obj).modepost()
				if (resdata == 'success') {
					this.alertobj.diagfun('操作成功')
					//插入预约数据表
					let userinfo = JSON.parse(uni.getStorageSync('userinfo'))
					let objext = {
						people : userinfo.username,
						seatid : id,
						datastr : this.form.starttime + "至" + this.form.endtime,
						account : userinfo.account
					}
					let res= await new this.Request(this.Urls.m().appointmentinsert, objext).modepost()
					uni.navigateTo({
						url: `../msgsuccess/msgsuccess?people=${objext.people}&seatid=${id}&datastr=${objext.datastr}&account=${objext.account}`
					});
					//并且跳转预约界面（包含二维码）

					// setTimeout(() => {
					// 	this.queryseat(0)
					// }, 1500)
				}

			},
			tabSelect(e) {
				this.TabCur = e.currentTarget.dataset.id;
				this.queryseat(this.TabCur)
				this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
			},
			deletefun(val) {
				uni.showModal({
					content: '确定要删除吗？',
					cancelText: '取消',
					confirmText: '确定',
					success: res => {
						if (res.confirm) {
							this.asdeletefun(val)
						}
					}
				})
			},
			async asdeletefun(val) {
				let obj = {
					id: val
				}
				let resdata = await new this.Request(this.Urls.m().seatdelete, obj).modepost()
				if (resdata == 'success') {
					this.alertobj.diagfun('操作成功')
					setTimeout(() => {
						this.queryseat(0)
					}, 1500)
				}
			}

		}
	}
</script>

<style scoped>
	.mainsub {
		padding-left: 5%;
		padding-right: 5%;
	}

	.card-main {
		width: 100%;
		height: 130px;
		background-color: #f1f1f1;
		border-radius: 5px;
		margin-top: 10px;
		padding: 5%;
		position: relative;
		border: 1px #f1f1f1 solid;
		box-shadow: darkgrey 2px 2px 2px 2px;
	}

	.card-top {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	.card-content {
		display: flex;
		justify-content: flex-start;
		align-items: center;
	}


	.card-maintitle01 {
		font-weight: bold;
	}

	.card-maintitle01,
	.card-maintitle02 {
		margin-top: 10px;
	}

	.card-toptitle02 {
		margin-top: 10px;
	}

	.rightdetail {
		display: flex;
		justify-content: center;
		align-items: center;
	}

	.text-xsl {
		margin-top: 20%;
		text-align: center;
		font-size: 120px;
	}

	.textclass {
		margin-top: 5px;
		font-weight: 600;
		font-size: 15px;
		color: darkgray;
	}
</style>
